<template>
  <li class="song-list-item" @click="$emit('showSetails',item.id)">
    <div class="img">
      <img :src="item.picUrl" alt />
      <div class="play-count">
        <i class="fa fa-headphones"></i>
        {{ playCount}}
      </div>
    </div>
    <div class="title">{{item.name}}</div>
  </li>
</template>

<script>
export default {
  name: "conglistitem",
  props: ["item"],
  methods: {
    setCount: function() {
      if (this.item.playCount > 99999999) {
        return (this.item.playCount / 100000000).toFixed(1) + "亿";
      } else if (this.item.playCount > 9999) {
        return (this.item.playCount / 10000).toFixed(1) + "万";
      } else {
        return this.item.playCount;
      }
    }
  },

  computed: {
    playCount: function() {
      return this.setCount();
    }
  }
};
</script>

<style  lang="less" scoped>
.song-list-item {
  padding-bottom: 10px;
  .img {
    position: relative;
    img {
      display: block;
      width: 100%;
      border-radius: 3px;
    }
    .play-count {
      position: absolute;
      top: 0;
      right: 0;
      color: #fff;
      font-size: 12px;
      padding: 0 5px;
      i {
        font-size: 12px;
        margin-right: 5px;
      }
    }
  }
  .title {
    font-size: 12px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin: 5px 0;
    color: #4f4f4f;
    overflow: hidden;
  }
}
</style>