<template>
  <div class="playing-list" @click="$emit('closeList')">
    <div class="top">
      <ul class="title">
        <li>共{{PlayingArr.length}}首</li>
        <li></li>
      </ul>
      <ul class="list">
        <SongItem
          v-for="(item, index) in PlayingArr"
          :key="index"
          :Song="item"
          :index="index"
          :isShowIcon="false"
          @toPlay="$emit('toPlay',$event)"
          @deleteSong="$emit('deleteSong',$event)"
        ></SongItem>
      </ul>
    </div>
  </div>
</template>

<script>
import SongItem from "./SongItem";
export default {
  name: "PlayingList",
  props: ["PlayingArr"],

  components: {
    SongItem
  }
};
</script>

<style lang="less" scoped>
.playing-list {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  .top {
    width: 100%;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;

    .title {
      border-bottom: 1px solid #ddd;
      padding: 15px;
    }

    .list {
      height: 50vh;

      overflow-y: auto;
      li {
        border-top: 1px solid #ddd;
        padding: 5px 15px;
        p:nth-child(2) {
          line-height: 16px;
        }

        &:nth-child(1) {
          border-top: none;
        }
      }
    }
  }
}
</style>