<template>
  <div class="mine">
    <ul class="list">
      <li>
        <div class="icon">
          <i class="fa fa-music"></i>
        </div>
        <div class="title">
          本地音乐
          <span>(0)</span>
        </div>
      </li>
      <li>
        <div class="icon">
          <i class="fa fa-play-circle"></i>
        </div>
        <div class="title" @click="RecentList">
          最近播放
          <span>({{LatelyPlay}})</span>
        </div>
      </li>
      <li>
        <div class="icon">
          <i class="fa fa-download"></i>
        </div>
        <div class="title">
          下载管理
          <span>(0)</span>
        </div>
      </li>
      <li>
        <div class="icon">
          <i class="fa fa-podcast"></i>
        </div>
        <div class="title">
          我的电台
          <span>(0)</span>
        </div>
      </li>
      <li>
        <div class="icon">
          <i class="fa fa-user-circle"></i>
        </div>
        <div class="title">
          我的收藏
          <span>(0)</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "mine",
  data() {
    return {
      PlayingArr:[]
    }
  },
  methods: {
    RecentList(){
     
      this.$router.push({ path: "recentList" })
    }
  },
  computed: {
    LatelyPlay(){
      return this.PlayingArr.length>20?20:this.PlayingArr.length
    }
  },
  created(){
    let playingList=JSON.parse(localStorage.getItem('playingList'))
    this.PlayingArr=playingList?playingList:[]

  }
};
</script>

<style lang="less" scoped>
.mine {
  .list {
    color: #3b3b3b;
    border-top: 1px solid #ddd;
    li {
      line-height: 22px;
      overflow: hidden;
      &:nth-child(1) {
        div.title {
          border: none;
        }
      }

      div {
        padding: 10px 0;
        font-size: 14px;
        font-weight: bold;
        line-height: 22px;
        float: left;
        span{
          font-size: 12px;
          color:#999;
        }
        &.icon {
          width: 10%;
          padding-left: 15px;
        }
        &.title {
          margin-left: 10px;
          width: calc(90% - 10px);
          border-top: 1px solid #ddd;
        }
        i {
          font-size: 22px;
        }
      }
    }
  }
}
</style>