<template>
  <div class="player-bar">
    <ul class="bar" @click="ShowFull=true">
      <li class="pic">
        <div class="img" :class="{pause:!isPlay}">
          <div class="bg" :style="{backgroundImage:`url(${exactPicUrl})`}"></div>
        </div>
      </li>
      <li class="information">
        <p class="name">{{exactName}}</p>
        <p class="athor">{{exactAthor}}</p>
      </li>
      <li class="control">
        <vProgress
          class="progress"
          :Playing="isPlay"
          :percentage="ratio?ratio:0"
          :Width="30"
          :strokeWidth="4"
          type="circle"
          status="exception"
          @playBtnClick="$emit('playBtnClick',$refs.audio.src)"
        />
      </li>
      <li class="menu">
        <i class="fa fa-list-ul" @click.stop="$emit('showList')"></i>
      </li>
    </ul>
    <div class="fullscreen" v-if="ShowFull">
      <div class="cover" :style="{backgroundImage:`url(${exactPicUrl})`}"></div>
      <div class="content">
        <ul class="title">
          <li class="go-back">
            <i class="fa fa-arrow-left" @click="ShowFull=false"></i>
          </li>
          <li class="song">
            <p>{{exactName}}</p>
            <p>{{exactAthor}}</p>
          </li>
          <li class="icon">
            <i class="fa fa-share-alt"></i>
          </li>
        </ul>
        <div class="cd" :class="{pause:!isPlay}">
          <div class="bg" :style="{backgroundImage:`url(${exactPicUrl})`}"></div>
        </div>
        <div class="tou" :class="{active:isPlay}">
          <img src="/cd_tou.png" alt />
        </div>
        <div class="controls">
          <div class="progre">
            <div class="startTime">{{exactStartTime?exactStartTime:'00:00'}}</div>
            <div class="line">
              <vProgress :percentage="ratio?ratio:0" :showText="false" :strokeWidth="3"></vProgress>
            </div>
            <div class="endTime">{{exactEndTime?exactEndTime:'00:00'}}</div>
          </div>
          <ul class="control-btn">
            <li>
              <i class="fa fa-retweet"></i>
            </li>
            <li>
              <i class="fa fa-step-backward" @click="$emit('checkedSong','prev')"></i>
            </li>
            <li>
              <div class="play-btn" @click.stop="$emit('playBtnClick',$refs.audio.src)">
                <i class="fa fa-pause" v-if="isPlay"></i>
                <i class="fa fa-play" v-else @click="isPlay=false"></i>
              </div>
            </li>
            <li>
              <i class="fa fa-step-forward" @click="$emit('checkedSong','next')"></i>
            </li>
            <li>
              <i class="fa fa-bars" @click.stop="$emit('showList')"></i>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <audio ref="audio" :src="exactUrl" autoplay></audio>
  </div>
</template>

<script>
import vProgress from "./Progress";
export default {
  name: "PlayerBar",
  props: ["SongInformation", "isPlay", "PlayingArr"],
  data() {
    return {
      src:
        "http://img3.imgtn.bdimg.com/it/u=3119465150,2337696659&fm=26&gp=0.jpg",
      AudioSrc: null,
      ratio: 0,
      name: null,
      athor: null,
      ShowFull: false,
      startTime: null,
      endTime: null,
      playingitem: {}
    };
  },
  watch: {
    ShowFull(){
      this.$emit('isShowFull',this.ShowFull)
    },
    SongInformation() {
      this.$refs.audio.pause();
      this.AudioSrc = this.exactUrl;
      this.playingitem = {
        id: this.SongInformation.audioId,
        src: this.exactUrl?this.exactUrl:"http://img3.imgtn.bdimg.com/it/u=3119465150,2337696659&fm=26&gp=0.jpg",
        picUrl: this.SongInformation.picUrl,
        name: this.SongInformation.audioName,
        athor: this.SongInformation.athorArr
      };
      localStorage.setItem("playingitem", JSON.stringify(this.playingitem));
      this.$refs.audio.oncanplay = () => {
        this.$refs.audio.play();
        this.dosomething();
      };
    },
    isPlay() {
      if (this.isPlay) {
        this.$refs.audio.play();
        this.dosomething();
      } else {
        this.$refs.audio.pause();
      }
    }
  },
  components: {
    vProgress
  },
  created() {
    let playingitem = JSON.parse(localStorage.getItem("playingitem"));
    if (playingitem) {
      this.AudioSrc = playingitem.src;
      this.src = playingitem.picUrl;
      this.name = playingitem.name;
      this.athor = playingitem.athor;
      this.playingitem = playingitem;
    }
  },
  mounted() {
    this.$refs.audio.pause()
    this.$refs.audio.onended = () => {
      this.$emit("changeSong", this.playingitem);
    };
  },
  methods: {
    closeFull: function() {
      this.isShowFull = false;
    },
    dosomething: function() {
      setInterval(() => {
        if (this.$refs.audio.currentTime && this.$refs.audio.duration) {
          this.ratio =
            (this.$refs.audio.currentTime / this.$refs.audio.duration) * 100;
          this.startTime = this.$refs.audio.currentTime;
          this.endTime = this.$refs.audio.duration;
        }
      }, 500);
    }
  },
  computed: {
    exactStartTime() {
      let minute =
        parseInt(this.startTime / 60) < 10
          ? "0" + parseInt(this.startTime / 60)
          : parseInt(this.startTime / 60);
      let second =
        parseInt(this.startTime % 60) < 10
          ? "0" + parseInt(this.startTime % 60)
          : parseInt(this.startTime % 60);
      return minute + ":" + second;
    },
    exactEndTime() {
      let minute =
        parseInt(this.endTime / 60) < 10
          ? "0" + parseInt(this.endTime / 60)
          : parseInt(this.endTime / 60);
      let second =
        parseInt(this.endTime % 60) < 10
          ? "0" + parseInt(this.endTime % 60)
          : parseInt(this.endTime % 60);
      return minute + ":" + second;
    },
    exactPicUrl() {
      return this.SongInformation.picUrl
        ? this.SongInformation.picUrl
        : this.src;
    },
    exactName() {
      return this.SongInformation.audioName
        ? this.SongInformation.audioName
        : this.name;
    },
    exactAthor() {
      return this.SongInformation.athorArr
        ? this.SongInformation.athorArr
        : this.athor;
    },
    exactUrl() {
      return this.SongInformation.audioId
        ? "https://music.163.com/song/media/outer/url?id=" +
            this.SongInformation.audioId +
            ".mp3"
        : this.AudioSrc;
    }
  }
};
</script>

<style lang="less">
.player-bar {
  ul.bar {
    display: flex;
    width: 100%;
    padding: 5px 10px;
    li {
      flex: 1;
      padding-top: 5px;
      &.information {
        .athor {
          font-size: 12px;
          color: #999;
        }
      }
      &.control {
        text-align: right;
        .progress {
          text-align: center;
        }
      }
      &.pic {
        .img {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          overflow: hidden;
          animation: playing 5s linear 0s infinite;
          .bg {
            width: 100%;
            height: 100%;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
          }
          &.pause {
            animation-play-state: paused;
          }
        }
      }
      &.information {
        flex: 5;
        p {
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
      &.menu {
        text-align: right;
        i {
          font-size: 26px;
          color: gray;
        }
      }
    }
  }
  .fullscreen {
    height: 100vh;
    width: 100vw;
    position: relative;
    .cover {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-size: cover;
      background-position: center;
      filter: blur(5px);
    }
    .content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.8);
      ul.title {
        color: #fff;
        padding: 15px;
        display: flex;
        .song {
          flex: 1;
          padding: 0 20px;
          p {
            font-size: 14px;
            &:nth-child(2) {
              color: #6f6f6f;
            }
          }
        }
        .go-back,
        .icon {
          font-size: 20px;
          line-height: 38px;
        }
      }
      .cd {
        background-image: url("/disc-ip6.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 60vw;
        height: 60vw;
        padding: 12vw;
        position: absolute;
        top: 20vh;
        left: 0;
        right: 0;
        margin: auto;
        animation: playing 30s linear 0s infinite;
        @media screen and (min-width: 1024px) {
          width: 300px;
          height: 300px;
          padding: 60px;
        }
        .bg {
          height: 100%;
          width: 100%;
          border-radius: 50%;
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
        }
        &.pause {
          animation-play-state: paused;
        }
        img {
          width: 100%;
          display: block;
          border-radius: 50%;
        }
      }
      .tou {
        text-align: center;
        position: absolute;
        height: 160px;
        width: 50%;
        top: 0;
        left: 50%;
        transform: translateX(-50%) rotate(-60deg);
        z-index: 1;
        transition: all 0.3s;
        &.active {
          transform: translateX(-50%) rotate(-45deg);
          animation: rotated 3s ease infinite;
        }
        @media screen and (min-width: 1024px) {
          top: 10px;
        }
        img {
          position: absolute;
          bottom: 0;
          height: 45vw;
          top: 50%;
          left: 50%;
          transform: translateX(-50%);
          @media screen and (min-width: 1024px) {
            height: 200px;
          }
        }
      }
      .controls {
        position: absolute;
        padding: 15px;
        bottom: 0;
        left: 0;
        width: 100%;
        color: #fff;
        .progre {
          display: flex;
          .startTime,
          .endTime {
            font-size: 12px;
          }
          .line {
            text-align: center;
            padding: 0 10px;
            flex: 1;
            .progress {
              .progress-bar {
                margin: 7px 0;
                padding-right: 0;
              }
            }
          }
        }
        .control-btn {
          display: flex;
          padding: 10px 0 5px;
          li {
            flex: 1;
            text-align: center;
            line-height: 50px;
            font-size: 20px;
            .play-btn {
              margin: 0 auto;
              width: 50px;
              height: 50px;
              border: 1px solid #fff;
              border-radius: 50%;
              i {
                line-height: 50px;
              }
            }
          }
        }
      }
    }
  }
}
@keyframes rotated {
  from {
    transform: translateX(-50%) rotate(-40deg);
  }
  to {
    transform: translateX(-50%) rotate(-35deg);
  }
}
@keyframes playing {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
</style>