<template>
  <div id="app">
    <router-view :ShowFull='ShowFull' @toPlay="setUrl($event)" @playSearch="playSearch($event)"/>
    <PlayingList
      v-show="isShowList"
      @closeList="isShowList=false"
      :PlayingArr="PlayingArr"
      @toPlay="setUrl($event)"
      @deleteSong="deleteSong($event)"
    ></PlayingList>
    <div class="player">
      <playerBar
      @isShowFull='isShowFull($event)'
        @changeSong="changeSong($event)"
        :PlayingArr="PlayingArr"
        :SongInformation="SongInformation"
        @playBtnClick=" playCheck($event)"
        :isPlay="isPlay"
        @showList="isShowList=true"
        @checkedSong="checkedSong($event)"
      ></playerBar>
    </div>
  </div>
</template>
<script>
import PlayerBar from "./components/PlayerBar";
import PlayingList from "./components/PlayingList";
export default {
  data() {
    return {
      Url: null,
      picUrl: null,
      audioName: null,
      athorArr: [],
      isPlay: false,
      isShowList: false,
      PlayingArr: [],
      ShowFull:false,
    };
  },
  created() {
    this.PlayingArr = JSON.parse(localStorage.getItem("playingList"))
      ? JSON.parse(localStorage.getItem("playingList"))
      : [];
  },

  methods: {
    isShowFull($event){
this.ShowFull=$event
    },
    checkedSong($event) {
      let newIndex = 0;
      let playingitem = JSON.parse(localStorage.getItem("playingitem"));
      this.PlayingArr.forEach((element, index) => {
        if (element.id == playingitem.id) {
          newIndex = index;
        }
      });
      if ($event == "prev") {
        newIndex = newIndex - 1 < 0 ? this.PlayingArr.length - 1 : newIndex - 1;
      } else if ($event == "next") {
        newIndex = newIndex + 1 >= this.PlayingArr.length ? 0 : newIndex + 1;
      }
      this.cutSong(newIndex);
    },
    deleteSong($event) {
      let playingitem = JSON.parse(localStorage.getItem("playingitem"));
      if (this.PlayingArr[$event].id == playingitem.id) {
        this.PlayingArr.splice($event, 1);
        let artist = "";
        this.PlayingArr[$event].ar.forEach(element => {
          artist += element.name;
        });
        localStorage.setItem(
          "playingitem",
          JSON.stringify({
            id: this.PlayingArr[$event].id,
            src: `https://music.163.com/song/media/outer/url?id=${this.PlayingArr[$event].id}.mp3`,
            picUrl: this.PlayingArr[$event].al.picUrl,
            name: this.PlayingArr[$event].name,
            athor: artist
          })
        );
      } else {
        this.PlayingArr.splice($event, 1);
      }
      localStorage.setItem("playingList", JSON.stringify(this.PlayingArr));
    },
    playSearch: function($event) {
      let already = false;
      this.audioName = $event.name;
      this.Url = $event.id;
      this.picUrl = $event.album.artist.img1v1Url;
      let b = [];
      for (let i = 0; i < $event.artists.length; i++) {
        let a = { name: $event.artists[i].name };
        b.push(a);
      }
      this.athorArr = b;
      this.isPlay = true;
      let obj = {
        name: $event.name,
        id: $event.id,
        ar: $event.artists,
        al: { picUrl: $event.album.artist.img1v1Url }
      };
      for (let i = 0; i < this.PlayingArr.length; i++) {
        if (this.PlayingArr[i].id == obj.id) {
          already = true;
        }
      }
      if (!already) {
        this.PlayingArr.unshift(obj);
        localStorage.setItem("playingList", JSON.stringify(this.PlayingArr));
      }
    },
    playCheck: function($event) {
      if (!$event) {
        return;
      }
      this.isPlay = !this.isPlay;
    },
    setUrl: function($event) {
      let already = false;
      this.Url = $event.id;
      this.picUrl = $event.al.picUrl;
      this.audioName = $event.name;
      this.athorArr = $event.ar;
      this.isPlay = true;
      let obj = $event;
      for (let i = 0; i < this.PlayingArr.length; i++) {
        if (this.PlayingArr[i].id == obj.id) {
          already = true;
        }
      }
      if (!already) {
        this.PlayingArr.unshift(obj);
        localStorage.setItem("playingList", JSON.stringify(this.PlayingArr));
      }
    },
    changeSong($event) {
      let indexOfSong = 0;
      this.PlayingArr.forEach((element, index) => {
        if ($event.id == element.id) {
          indexOfSong = index;
        }
      });
      indexOfSong =
        indexOfSong + 1 >= this.PlayingArr.length ? 0 : indexOfSong + 1;
      this.cutSong(indexOfSong);
    },
    cutSong(indexOfSong) {
      this.picUrl = this.PlayingArr[indexOfSong].al.picUrl;
      this.Url = this.PlayingArr[indexOfSong].id;
      this.audioName = this.PlayingArr[indexOfSong].name;
      this.athorArr = this.PlayingArr[indexOfSong].ar;
    }
  },
  computed: {
    SongInformation: function() {
      return {
        picUrl: this.picUrl,
        audioName: this.audioName,
        athorArr: this.athor,
        audioId: this.Url
      };
    },
    athor: function() {
      let a = "";
      for (let i = 0; i < this.athorArr.length; i++) {
        a += this.athorArr[i].name + " ";
      }
      return a;
    }
  },
  components: {
    PlayerBar,
    PlayingList
  }
};
</script>

<style lang="less">
#app {
  padding-bottom: 53px;
  .player {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    border-top: 1px solid #ddd;
    audio {
      height: 50px;
      width: 100%;
    }
  }
  .playing-list {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
  }
}
</style>
