<template>
  <div class="progress" :class="[status?`is-${status}`:'',`progress-${type}`]" @click.stop="play">
    <div class="progress-bar" v-if="type=='line'">
      <div class="progress-bar_outer" :style="{height:strokeWidth+'px'}">
        <div class="progress-bar_inner" :style="barStyle">
          <div
            class="progress-bar-innerText"
            :style="{lineHeight:strokeWidth+'px'}"
            v-if="textInside&&showText"
          >{{percentage}}%</div>
        </div>
      </div>
    </div>

    <div class="progress-circles" :style="{width:Width+'px',height:Width+'px'}" v-else>
      <svg viewBox="0 0 100 100">
        <path :d="trackPath" fill="none" :stroke-width="relativeStrokeWidth" stroke="#666" />
        <path
          :d="trackPath"
          fill="none"
          :stroke-width="relativeStrokeWidth"
          :stroke="strokeFill"
          :style="circlePathStyle"
          stroke-linecap="round"
        />
      </svg>
    </div>

    <div
      class="progress-text"
      :style="{fontSize:progressTextSize+'px'}"
      v-if="textInside==false&&showText"
    >
      <i v-if="Playing" class="icon fa fa-pause"></i>
      <i v-else class="icon fa fa-play" ></i>
    </div>
  </div>
</template>
<script>
export default {
  name: "Progress",
  props: {
    Playing:{
      type:Boolean,
      dafault:false
    },
    //环形进度条边长
    Width: {
      type: Number,
      default: 126
    },
    //进度条颜色
    color: {
      type: String
    },
    //是否显示文字
    showText: {
      type: Boolean,
      default: true
    },
    //百分比内显
    textInside: {
      type: Boolean,
      dafault: false
    },
    //进度条高度
    strokeWidth: {
      type: Number,
      default: 6
    },
    //百分比
    percentage: {
      type: Number,
      required: true,
      default: 0,
      validator: val => val >= 0 && val <= 100
    },
    //状态--succes/execption
    status: {
      type: String
    },
    //进度条类型--线性:line/环形:circle
    type: {
      type: String,
      default: "line",
      validator: val => ["circle", "line"].includes(val)
    }
  },
  
  methods:{
    play:function(){
      this.$emit('playBtnClick')
    }
  },
  computed: {
    perimeter: function() {
      const radius = 50 - this.relativeStrokeWidth / 2;
      return 2 * Math.PI * radius;
    },
    circlePathStyle: function() {
      const perimeter = this.perimeter;
      return {
        strokeDasharray: `${perimeter}px,${perimeter}px`,
        strokeDashoffset: (1 - this.percentage / 100) * perimeter + "px"
      };
    },
    relativeStrokeWidth: function() {
      return (this.strokeWidth * 50) / this.Width;
    },
    trackPath: function() {
      const radius = 50 - this.relativeStrokeWidth / 2;
      return `
      M 50 50
      m 0 -${radius} 
      a ${radius} ${radius} 0 1 1 0 ${radius * 2} 
      a ${radius} ${radius} 0 1 1 0 -${radius * 2}`;
    },
    iconClass: function() {
      if (this.type == "line") {
        return this.status == "succes"
          ? "icon-circle-check"
          : "icon-circle-close";
      } else {
        return this.status == "succes" ? "icon-check" : "icon-close";
      }
    },
    progressTextSize: function() {
      return 12 + this.strokeWidth * 0.4;
    },
    strokeFill: function() {
      let color;
      if (this.color) {
        return this.color;
      }
      switch (this.status) {
        case "succes":
          color = "#13ce66";
          break;
        case "exception":
          color = "#ff4949";
          break;
        default:
          color = "#20a0ff";
      }
      return color;
    },
    barStyle: function() {
      return {
        width: this.percentage + "%",
        background: this.strokeFill
        // backgroundColor: this.strokeFill
      };
    }
  }
};
</script>
<style scoped>

.icon {
  display: block;
  color:#666;
  margin-left: 1px;
  
  font-size: 10px;
  font-style: normal;
}

.progress.is-succes .progress-text {
  color: #67c23a;
}
.progress.is-exception .progress-text {
  color: #f56c6c;
}
.progress-bar {
  /* width: calc(100% - 50px); */
  width: 100%;
  display: inline-block;
  padding-right: 50px;
  box-sizing: border-box;
  margin-right: -50px;
}
.progress-bar_outer {
  background-color: #ebeef5;
  border-radius: 100px;
}
.progress-bar_inner {
  height: 100%;
  border-radius: 100px;
  transition: width 0.3s linear;
  text-align: right;
  line-height: 1;
}
.progress-bar-innerText {
  height: 100%;
  color: #fff;
  display: inline-block;
  font-size: 12px;
  margin: 0 10px;
}
.progress-text {
  display: inline-block;
  color: #606266;
  margin-left: 10px;
  
}
.progress-circle {
  display: inline-block;
  position: relative;
}
.progress-circle .progress-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  width: 100%;
  text-align: center;
  margin-left: 0;
}
</style>
