<template>
  <div class="find">
    <Banner></Banner>
    <Title name="推荐歌单" tag="歌单广场" @goToMore="goToSquare"></Title>
    <ul class="song-list">
      <SongListItem
        v-for="(item, index) in SongList"
        :key="index"
        :item="item"
        @showSetails="showSetails($event)"
      ></SongListItem>
    </ul>
    <Title name="新碟" tag="更多新碟" @goToMore="albumSquare"></Title>
    <ul class="album-list">
      <AlbumItem v-for="(item, index) in newDish" :key="index" :Album="item"></AlbumItem>
    </ul>
  </div>
</template>

<script>
import Title from "../components/Title";
import SongListItem from "../components/SongListItem";
import Banner from "../components/Banner";
import AlbumItem from "../components/AlbumItem";

export default {
  name: "find",
  data() {
    return {
      SongList: [],
      newDish: []
    };
  },
  methods: {
    albumSquare(){
      this.$router.push({ path: "albumSquare" });
    },
    goToSquare(){
      this.$router.push({ path: "songlistsquare" });
    },
    showSetails: function(id) {
      this.$router.push({ path: "playlist", query: { id: id } });
    }
  },
  created() {
    let cloudData = JSON.parse(localStorage.getItem("cloudData"));
    if (cloudData && (cloudData.find.time + 1000 * 60 > Date.now())) {
      this.SongList = cloudData.find.data;
    } else {
      //推荐歌单http://musicapi.leanapp.cn
      this.axios
        .get("https://music.kele8.cn/personalized?limit=6")
        .then(res => {
          this.SongList = res.data.result;
          let cloudData = { find: { data: res.data.result, time: Date.now() } };
          localStorage.setItem("cloudData", JSON.stringify(cloudData));
        });
    }
    //新碟
    this.axios
      .get("https://music.kele8.cn/top/album?offset=0&limit=3")
      .then(res => {
        this.newDish = res.data.albums;
      });
  },
  components: {
    Title,
    SongListItem,
    Banner,
    AlbumItem
  }
};
</script>

<style  lang="less" scoped>
.find {
  .song-list,
  .album-list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 15px;
    li {
      flex: 1 0 30%;
      &:nth-child(3n + 2) {
        margin: 0 5px;
      }
    }
  }
}
</style>