<template>
  <li class="song-item" @click="$emit('toPlay',Song)">
    <span class="num" v-if="isShowIcon">{{index+1}}</span>
    <div class="information">
      <p class="name">{{Song.name}}</p>
      <p class="athor">{{athor}}</p>
    </div>
    <div class="icon" v-if="isShowIcon">
      <i class="fa fa-play-circle" @click="$emit('toPlay',Song)"></i>
      <i class="fa fa-ellipsis-v"></i>
    </div>
    <div class="del" v-else>
      <i class="fa fa-trash" @click.stop="$emit('deleteSong',index)"></i>
    </div>
  </li>
</template>

<script>
export default {
  name: "SongItem",
  props: ["Song", "index", "isShowIcon"],
  computed: {
    athor: function() {
      let a = "";
      for (let i = 0; i < this.Song.ar.length; i++) {
        a += this.Song.ar[i].name + " ";
      }
      return a;
    }
  }
};
</script>

<style lang="less" scoped>
.song-item {
  display: flex;
  padding: 5px 0;
  .num {
    color: #999;
    line-height: 47px;
    margin-right: 10px;
  }
  div {
    &.information {
      flex: 1;
      .name {
        font-size: 14px;
        color: #333;
        line-height: 20px;
      }
      .athor {
        font-size: 12px;
        color: #999;
        line-height: 20px;
      }
    }
    &.icon {
      text-align: right;
      font-size: 12px;
      line-height: 47px;
      i {
        padding: 0 10px;
        color: #999;
      }
    }
    &.del {
      line-height: 47px;
      text-align: right;
      font-size: 14px;
      i {
        padding: 0 10px;
        color: #999;
      }
    }
  }
}
</style>