<template>
  <div class="cloud">
    <template v-if="HotCommentArr">
      <ul class="list">
      <CommentItem v-for="(item, index) in HotCommentArr" :key="index" :item="item"></CommentItem>
    </ul>
    <div class="bottom">到底咯~~~</div>
    </template>
    <div class="loading" v-else>加载中</div>
  </div>
</template>

<script>
import CommentItem from "@/components/CommentItem";
export default {
  name: "cloud",
  data() {
    return {
      HotCommentArr: null
    };
  },
  created() {
    this.axios.get(`http://music.kele8.cn/comment/hotwall/list`).then(res => {
      this.HotCommentArr = res.data.data;
    });
  },
  components: {
    CommentItem
  }
};
</script>

<style lang="less" scoped>
.cloud {
  padding: 10px;
  overflow: auto;
  background-color: #f8f8f8;
  width: 100%;
  column-count: 2;
  column-gap: 10px;

  .comment-item {
    width: 100%;
    break-inside: avoid;
  }
  .bottom{
    text-align: center;
    padding:15px 0; 
    font-size:14px;
    color:#666;
  }
}
</style>