<template>
  <div class="banner">
    <ul class="banner-img">
      <li
        class="img"
        v-for="(item, index) in bannerUrls"
        :key="index"
        :class="{active:index==bannerIndex}"
      >
        <img :src="item.picUrl" alt />
      </li>
    </ul>
    <ul class="bannder-index">
      <li v-for="(item, index) in bannerUrls" :key="index" >
        <i :class="{active:index==bannerIndex}"></i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Banner",
  data() {
    return {
      bannerUrls: [],
      bannerIndex: 0
    };
  },
  methods: {
    setIndex: function() {
      setInterval(() => {
        this.bannerIndex++;
        if (this.bannerIndex >= this.bannerUrls.length) {
          this.bannerIndex = 0;
        }
      }, 5000);
    }
  },
  computed: {},
  created() {
    this.axios.get("http://musicapi.leanapp.cn/banner?type=1").then(res => {
      this.bannerUrls = res.data.banners;
    });
    this.setIndex();
  }
};
</script>

<style lang="less" scoped>
.banner {
  overflow: hidden;
  position: relative;
  ul.banner-img {
    position: relative;
    .img {
      transition: all 1s;
      padding: 0 15px;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      &.active {
        opacity: 1;
      }
      &:nth-child(1) {
        position: static;
      }
      img {
        border-radius: 5px;
        width: 100%;
        display: block;
      }
    }
  }
  .bannder-index {
    width: 100%;
    display: flex;
    padding: 0 25vw;
    position: absolute;
    left:0;
    bottom: 5px;
    li {
      flex: 1;
      text-align:center;

      i {
        width: 7px;
        height: 7px;
        display: inline-block;
        
        background-color: rgba(0, 0, 0, .5);
        border-radius: 50%;
        &.active{
          background-color: orange;
        }
      }
    }
  }
}
</style>